import React, { Component } from 'react';
import { Grid, Row, Col, ButtonGroup, Button, DropdownButton, MenuItem } from 'react-bootstrap';
import List from './List';

class FilteredList extends Component {
  constructor(props) {
    super(props);

    this.state = {
      search: "",
      filterColor: "All",
      filterPrice: "All",
      sortPrice: "None",
      sortName: "None",
    };
  }

  // Sets the state whenever the user types on the search bar
  onSearch = (event) => {
    this.setState({search: event.target.value.trim().toLowerCase()});
  }

  onFilterColor = (event) => {
    this.setState({filterColor: event})
  }

  onFilterPrice = (event) => {
    this.setState({filterPrice: event})
  }

  inPriceRange = (filterPrice, itemPrice) => {
    switch (filterPrice) {
      case "All": return true;
      case "Less than $100": return itemPrice < 100;
      case "$100 to $199": return itemPrice >= 100 && itemPrice <= 199;
      case "$200 to $299": return itemPrice >= 200 && itemPrice <= 299;
      case "$300 or more": return itemPrice >= 300;
      default: return true;
    }
  }

  filterItem = (item) => {
    return item.name.toLowerCase().search(this.state.search) !== -1
      && (this.state.filterColor === "All" || this.state.filterColor === item.color)
      && this.inPriceRange(this.state.filterPrice, item.price);
  }

  sortItem = (itemA, itemB) => {
    if (this.state.sortPrice !== "None" && itemA.price - itemB.price !== 0) {
      return this.state.sortPrice === "Lower"
        ? itemA.price - itemB.price : itemB.price - itemA.price;
    }
    let nameA = itemA.name.toUpperCase();
    let nameB = itemB.name.toUpperCase();
    if (this.state.sortName !== "None" && nameA !== nameB) {
      if (this.state.sortName === "A to Z") {
        return nameA < nameB ? -1 : 1;
      } else {
        return nameA > nameB ? -1 : 1;
      }
    }
    // default to natural ordering if no sorts specified
    return itemA.index - itemB.index;
  }

  render(){
    return (
        <Grid fluid>
          <Row className="title"><h1>Monopoly Property Search</h1></Row>
          <Grid>
            <Row className="filter-row">
              <Col md={4} xs={12}>
                <h2>Filter</h2>
                <Col lg={6} md={12}>
                  <h4>By Color</h4>
                    <DropdownButton id="filterColor" title={this.state.filterColor}>
                      <MenuItem
                        eventKey="All"
                        id="All"
                        active={this.state.filterColor === "All"}
                        onSelect={this.onFilterColor}>
                        All
                      </MenuItem>
                      <MenuItem
                        eventKey="Dark Purple"
                        id="Dark Purple"
                        active={this.state.filterColor === "Dark Purple"}
                        onSelect={this.onFilterColor}>
                        Dark Purple
                      </MenuItem>
                      <MenuItem
                        eventKey="Light Blue"
                        id="Light Blue"
                        active={this.state.filterColor === "Light Blue"}
                        onSelect={this.onFilterColor}>
                        Light Blue
                      </MenuItem>
                      <MenuItem
                        eventKey="Pink"
                        id="Pink"
                        active={this.state.filterColor === "Pink"}
                        onSelect={this.onFilterColor}>
                        Pink
                      </MenuItem>
                      <MenuItem
                        eventKey="Orange"
                        id="Orange"
                        active={this.state.filterColor === "Orange"}
                        onSelect={this.onFilterColor}>
                        Orange
                      </MenuItem>
                      <MenuItem
                        eventKey="Red"
                        id="Red"
                        active={this.state.filterColor === "Red"}
                        onSelect={this.onFilterColor}>
                        Red
                      </MenuItem>
                      <MenuItem
                        eventKey="Yellow"
                        id="Yellow"
                        active={this.state.filterColor === "Yellow"}
                        onSelect={this.onFilterColor}>
                        Yellow
                      </MenuItem>
                      <MenuItem
                        eventKey="Green"
                        id="Green"
                        active={this.state.filterColor === "Green"}
                        onSelect={this.onFilterColor}>
                        Green
                      </MenuItem>
                      <MenuItem
                        eventKey="Blue"
                        id="Blue"
                        active={this.state.filterColor === "Blue"}
                        onSelect={this.onFilterColor}>
                        Blue
                      </MenuItem>
                    </DropdownButton>
                </Col>
                <Col lg={6} md={12}>
                  <h4>By Price Range</h4>
                    <DropdownButton id="filterPrice" title={this.state.filterPrice}>
                      <MenuItem
                        eventKey="All"
                        id="All"
                        active={this.state.filterPrice === "All"}
                        onSelect={this.onFilterPrice}>
                        All
                      </MenuItem>
                      <MenuItem
                        eventKey="Less than $100"
                        id="Less than $100"
                        active={this.state.filterPrice === "Less than $100"}
                        onSelect={this.onFilterPrice}>
                        Less than $100
                      </MenuItem>
                      <MenuItem
                        eventKey="$100 to $199"
                        id="$100 to $199"
                        active={this.state.filterPrice === "$100 to $199"}
                        onSelect={this.onFilterPrice}>
                        $100 to $199
                      </MenuItem>
                      <MenuItem
                        eventKey="$200 to $299"
                        id="$200 to $299"
                        active={this.state.filterPrice === "$200 to $299"}
                        onSelect={this.onFilterPrice}>
                        $200 to $299
                      </MenuItem>
                      <MenuItem
                        eventKey="$300 or more"
                        id="$300 or more"
                        active={this.state.filterPrice === "$300 or more"}
                        onSelect={this.onFilterPrice}>
                        $300 or more
                      </MenuItem>
                    </DropdownButton>
                </Col>
              </Col>
              <Col md={4} sm={12}>
                <h2>Search</h2>
                <input className="search-bar" type="text" placeholder="Search properties" onChange = {this.onSearch} />
              </Col>
              <Col md={4} sm={12}>
                <h2>Sort</h2>
                <Col lg={6} md={12}>
                  <h4>By Price</h4>
                  <ButtonGroup>
                    <Button
                      active={this.state.sortPrice === "Lower"}
                      onClick={() => this.setState({
                        sortPrice: this.state.sortPrice === "Lower" ? "None" : "Lower"
                      })}>
                      Lower
                    </Button>
                    <Button
                      active={this.state.sortPrice === "Higher"}
                      onClick={() => this.setState({
                        sortPrice: this.state.sortPrice === "Higher" ? "None" : "Higher"
                      })}>
                      Higher
                    </Button>
                  </ButtonGroup>
                </Col>
                <Col lg={6} md={12}>
                  <h4>By Name</h4>
                  <ButtonGroup>
                    <Button
                      active={this.state.sortName === "A to Z"}
                      onClick={() => this.setState({
                        sortName: this.state.sortName === "A to Z" ? "None" : "A to Z"
                      })}>
                      A to Z
                    </Button>
                    <Button
                      active={this.state.sortName === "Z to A"}
                      onClick={() => this.setState({
                        sortName: this.state.sortName === "Z to A" ? "None" : "Z to A"
                      })}>
                      Z to A
                    </Button>
                  </ButtonGroup>
                </Col>
              </Col>
            </Row>
            <hr className="divider" />
            <List items = {this.props.items.filter(this.filterItem).sort(this.sortItem)} />
          </Grid>
        </Grid>
    );
  }
}

export default FilteredList;
