import React, {Component} from 'react';
import MonopolyCard from './MonopolyCard';
import {Row} from 'react-bootstrap';

class List extends Component {
  renderList() {
    const items = this.props.items.map(item => {
      return <MonopolyCard
        key={item.name}
        name={item.name}
        color={item.color}
        price={item.price}
        image={item.image}/>
    });

    return items;
  }

  render() {
    return (
      <Row>
        {this.renderList()}
      </Row>
    );
  }
}

export default List;
