import React, {Component} from 'react';
import {Col} from 'react-bootstrap';

class MonopolyCard extends Component {
  getColorClass = (color) => {
    return color.toLowerCase().split(" ").join("-");
  }

  render() {
    return (
      <Col xs={12} sm={6} md={4}>
        <div className="monopoly-card">
          <div className={`colored-top ${this.getColorClass(this.props.color)}`}></div>
          <h3>{this.props.name.toLowerCase()}</h3>
          <div>
            <img className="property-image" alt={this.props.name} src={require(`${this.props.image}`)}/>
          </div>
          <div className="price-row">price  <span className="price">{`$${this.props.price}`}</span></div>
        </div>
      </Col>
    );
  }
}

export default MonopolyCard;
